import React, { Component, } from "react"
import PropTypes from "prop-types"
import { List } from "immutable"
import ImPropTypes from "react-immutable-proptypes"
import { sanitizeUrl } from "core/utils"
import ModelTable from "./model-table"
import {toStep} from "./model-table-utils"


const braceOpen = "{"
const braceClose = "}"
const propClass = "property44"

export default class ObjectModelTable extends Component {
  static propTypes = {
    schema: PropTypes.object.isRequired,
    getComponent: PropTypes.func.isRequired,
    getConfigs: PropTypes.func.isRequired,
    expanded: PropTypes.bool,
    onToggle: PropTypes.func,
    specSelectors: PropTypes.object.isRequired,
    name: PropTypes.string,
    displayName: PropTypes.string,
    isRef: PropTypes.bool,
    required: PropTypes.bool,
    expandDepth: PropTypes.number,
    depth: PropTypes.number,
    specPath: ImPropTypes.list.isRequired,
    includeReadOnly: PropTypes.bool,
    includeWriteOnly: PropTypes.bool,
  }

  render(){
    let { schema, name, displayName, isRef, getComponent, getConfigs, depth, onToggle, expanded, specPath,required, ...otherProps } = this.props
    let { specSelectors,expandDepth, includeReadOnly, includeWriteOnly} = otherProps
    const { isOAS3 } = specSelectors

    if(!schema) {
      return null
    }

    const { showExtensions } = getConfigs()
    
    let description = schema.get("description")
    let properties = schema.get("properties")
    let additionalProperties = schema.get("additionalProperties")
    let title = schema.get("title") || displayName || name
    let requiredProperties = schema.get("required")
    let infoProperties = schema
      .filter( ( v, key) => ["maxProperties", "minProperties", "nullable", "example"].indexOf(key) !== -1 )
    let deprecated = schema.get("deprecated")
    let externalDocsUrl = schema.getIn(["externalDocs", "url"])
    let externalDocsDescription = schema.getIn(["externalDocs", "description"])
    const Markdown = getComponent("Markdown", true)
    const Link = getComponent("Link")



    const anyOf = specSelectors.isOAS3() ? schema.get("anyOf") : null
    const oneOf = specSelectors.isOAS3() ? schema.get("oneOf") : null
    const not = specSelectors.isOAS3() ? schema.get("not") : null
    return <>

      <tr>
        <td>{toStep(depth)}{name}</td>
        <td>{schema.get('type')}</td>
        <td>              {
                !description ? null : 
                      <Markdown source={ description } />
              }</td>
        <td>
          <span className="inner-object">
          {required&& <div className="external-docs">required</div>}
            {
              <table className="model"><tbody>

              {
                externalDocsUrl &&
                <tr className={"external-docs"}>
                  <td>
                    externalDocs:
                  </td>
                  <td>
                    <Link target="_blank" href={sanitizeUrl(externalDocsUrl)}>{externalDocsDescription || externalDocsUrl}</Link>
                  </td>
                </tr>
              }
              {
                !deprecated ? null :
                  <tr className={"property"}>
                    <td>
                      deprecated:
                    </td>
                    <td>
                      true
                    </td>
                  </tr>
              }
              
              {
                // empty row before extensions...
                !showExtensions ? null : <tr><td>&nbsp;</td></tr>
              }
              {
                !showExtensions ? null :
                  schema.entrySeq().map(
                    ([key, value]) => {
                      if(key.slice(0,2) !== "x-") {
                        return
                      }

                      const normalizedValue = !value ? null : value.toJS ? value.toJS() : value

                      return (<tr key={key} className="extension">
                        <td>
                          { key }
                        </td>
                        <td>
                          { JSON.stringify(normalizedValue) }
                        </td>
                      </tr>)
                    }).toArray()
              }
              </tbody></table>
          }
        </span></td>
      </tr>

      {
                !additionalProperties || !additionalProperties.size ? null
                  : <>
                      <tr>
                        <td>additionalProperties</td>
                        <td></td>
                        <td></td>
                        <td></td>
                      </tr>
                      <ModelTable { ...otherProps } required={ false }
                             getComponent={ getComponent }
                             specPath={specPath.push("additionalProperties")}
                             getConfigs={ getConfigs }
                             schema={ additionalProperties }
                             depth={ depth + 1 } />
                    
                  </>
              }
              {
                !anyOf ? null
                  : <>
                      <tr>
                        <td>{toStep(depth)}{name} { "anyOf ->" }</td>
                        <td></td>
                        <td></td>
                        <td></td>
                      </tr>
                      {anyOf.map((schema, k) => {
                        return <><ModelTable { ...otherProps } required={ false }
                                 getComponent={ getComponent }
                                 specPath={specPath.push("anyOf", k)}
                                 getConfigs={ getConfigs }
                                 schema={ schema }
                                 depth={ depth + 1 } /></>
                      })}
                    </>
              }
              {
                !oneOf ? null
                  : <>
                      {oneOf.map((schema, k) => {
                        return <>
                         <tr>
                    <td>{toStep(depth)}{name} oneOf({k+1}) {"->"}</td>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>
                        <ModelTable { ...otherProps } required={ false }
                                 getComponent={ getComponent }
                                 specPath={specPath.push("oneOf", k)}
                                 getConfigs={ getConfigs }
                                 schema={ schema }
                                 depth={ depth + 1 } /></>
                      })}
                  </>
              }
              {
                !not ? null
                  : <>
                  <tr>
                    <td>{ "not ->" }</td>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>
                        <ModelTable { ...otherProps }
                               required={ false }
                               getComponent={ getComponent }
                               specPath={specPath.push("not")}
                               getConfigs={ getConfigs }
                               schema={ not }
                               depth={ depth + 1 } />
                      </>
              }
      {
                !(properties && properties.size) ? null : properties.entrySeq().filter(
                    ([, value]) => {
                      return (!value.get("readOnly") || includeReadOnly) &&
                        (!value.get("writeOnly") || includeWriteOnly)
                    }
                ).map(
                    ([key, value]) => {
                      let isDeprecated = isOAS3() && value.get("deprecated")
                      let isRequired = List.isList(requiredProperties) && requiredProperties.contains(key)

                      let classNames = ["property-row"]

                      if (isDeprecated) {
                        classNames.push("deprecated")
                      }

                      if (isRequired) {
                        classNames.push("required")
                      }

                      return (<ModelTable key={ `object-${name}-${key}_${value}` } { ...otherProps }
                      required={ isRequired }
                      namePrimitive = {key}
                      getComponent={ getComponent }
                      specPath={specPath.push("properties", key)}
                      getConfigs={ getConfigs }
                      schema={ value }
                      depth={ depth + 1 } />)
                    }).toArray()
              }


    </>
  }
}
