import React, { Component, } from "react"
import PropTypes from "prop-types"
import ImPropTypes from "react-immutable-proptypes"
import ModelTable from "./model-table"

export default class ModelTableWrapper extends Component {

  static propTypes = {
    schema: PropTypes.object.isRequired,
    name: PropTypes.string,
    displayName: PropTypes.string,
    fullPath: PropTypes.array.isRequired,
    specPath: ImPropTypes.list.isRequired,
    getComponent: PropTypes.func.isRequired,
    getConfigs: PropTypes.func.isRequired,
    specSelectors: PropTypes.object.isRequired,
    expandDepth: PropTypes.number,
    layoutActions: PropTypes.object,
    layoutSelectors: PropTypes.object.isRequired,
    includeReadOnly: PropTypes.bool,
    includeWriteOnly: PropTypes.bool,
  }

  onToggle = (name,isShown) => {
    // If this prop is present, we'll have deepLinking for it
    if(this.props.layoutActions) {
      this.props.layoutActions.show(this.props.fullPath, isShown)
    }
  }


  render(){
    let { getConfigs } = this.props
    const generateUniqueId = () => `table-${Math.random().toString(36).substr(2, 9)}`
    const uniqueId = generateUniqueId()
    const copyTableNew = (tableId) => 
    {
      // Получаем элемент таблицы через document.querySelector
      const table = document.querySelector(`#${tableId}`)
  
      if (table) {
        // Создаем контейнер для копирования в виде документа
        const range = document.createRange()
        range.selectNode(table)
  
        // Очищаем выделение и создаем новое
        const selection = window.getSelection()
        selection.removeAllRanges()
        selection.addRange(range)
  
        try {
          // Копируем выделенный текст в буфер обмена
          document.execCommand("copy")
          alert("Table copied to clipboard!")
        } catch (err) {
          console.error("Failed to copy table: ", err)
        }
  
        // Очищаем выделение
        selection.removeAllRanges()
      } else {
        console.error("Table not found!")
      }
    }
    
  //   {
  //     const elTable = document.querySelector('#table-to-copied');
  //     let range, sel;
  //     // Ensure that range and selection are supported by the browsers
  //     if (document.createRange && window.getSelection) {
  
  //         range = document.createRange();
  //         sel = window.getSelection();
  //         // unselect any element in the page
  //         sel.removeAllRanges();
  
  //         try {
  //             range.selectNodeContents(elTable);
  //             sel.addRange(range);
  //         } catch (e) {
  //             range.selectNode(elTable);
  //             sel.addRange(range);
  //         }
  //         alert("Copied!")
  //         document.execCommand('Copy');
  //     }
  
  //     sel.removeAllRanges();

  // }

    let expanded
    if(this.props.layoutSelectors) {
      // If this is prop is present, we'll have deepLinking for it
      expanded = this.props.layoutSelectors.isShown(this.props.fullPath)
    }

    return <div className="model-box" key={uniqueId}>
      <div className="try-out">
      <button className="btn try-out__btn" onClick={()=>copyTableNew(uniqueId)}>Скопировать в буфер</button>
      </div>
<style>{`
  .table-to-copied{
    border: 1px solid #eee;
    table-layout: fixed;
    width: 100%;
    margin-bottom: 20px;
  }
  .table-to-copied th {
    font-weight: bold;
    padding: 5px;
    background: #efefef;
    border: 1px solid #dddddd;
  }
  .table-to-copied td{
    padding: 5px 10px;
    border: 1px solid #eee;
    text-align: left;
  }
  .table-to-copied tbody tr:nth-child(odd){
    background: #fff;
  }
  .table-to-copied tbody tr:nth-child(even){
    background: #F7F7F7;
  }
  `}</style>
      <table key={uniqueId} id={uniqueId} style={{}} className="table-to-copied">
        <thead>
        <tr>
          <th>Атрибут</th>
          <th>Тип</th>
          <th>Описание</th>
          <th>Свойства</th>
        </tr>
        </thead>
        <tbody>
          <ModelTable { ...this.props } getConfigs={ getConfigs } expanded={expanded} depth={ 1 } onToggle={ this.onToggle } expandDepth={ this.props.expandDepth || 0 }/>
        </tbody>
      </table>
    </div>
  }
}
