import React, { Component } from "react"
import PropTypes from "prop-types"
import { getExtensions, sanitizeUrl } from "core/utils"
import {toStep} from "./model-table-utils"
import EnumModelTable from "./enum-model-table"
import PropertyTable from "./property-table"

const propClass = "property primitive"

export default class PrimitiveTable extends Component {
  static propTypes = {
    schema: PropTypes.object.isRequired,
    getComponent: PropTypes.func.isRequired,
    getConfigs: PropTypes.func.isRequired,
    name: PropTypes.string,
    displayName: PropTypes.string,
    depth: PropTypes.number,
    required: PropTypes.bool,
    expandDepth: PropTypes.number
  }

  render() {
    let { schema, getComponent, getConfigs, name, displayName, depth, required} = this.props

    const { showExtensions } = getConfigs()

    if (!schema || !schema.get) {
      // don't render if schema isn't correctly formed
      return <div></div>
    }

    let type = schema.get("type")
    let format = schema.get("format")
    let xml = schema.get("xml")
    let enumArray = schema.get("enum")
    let title = schema.get("title") || displayName || name
    let description = schema.get("description")
    let extensions = getExtensions(schema)
    let properties = schema
      .filter((_, key) => ["enum", "type", "format", "description", "$$ref", "externalDocs"].indexOf(key) === -1)
      .filterNot((_, key) => extensions.has(key))
    let externalDocsUrl = schema.getIn(["externalDocs", "url"])
    let externalDocsDescription = schema.getIn(["externalDocs", "description"])

    const Markdown = getComponent("Markdown", true)
    //const EnumModel = getComponent("EnumModel")
    const Property = getComponent("Property")
    const Link = getComponent("Link")
{/* <pre>{ JSON.stringify(this.props, null, "\t") }</pre> */}



    return <>
    <tr>
    <td>{toStep(depth)}{name}</td>
    <td>{type}{format && <span className="prop-format">(${format})</span>}</td>
    <td>{
        !description ? null :
          <Markdown source={description} />
      }
    </td>
    <td>
    <span className="prop">
      {required&& <div className="external-docs">required</div>}
      {
        properties.size ? properties.entrySeq().map(([key, v]) => <PropertyTable key={`${key}-${v}`} propKey={key} propVal={v} propClass={propClass} />) : null
      }
      {
        showExtensions && extensions.size ? extensions.entrySeq().map(([key, v]) => <PropertyTable key={`${key}-${v}`} propKey={key} propVal={v} propClass={propClass} />) : null
      }
      {
        externalDocsUrl &&
        <div className="external-docs">
           <Link target="_blank" href={sanitizeUrl(externalDocsUrl)}>{externalDocsDescription || externalDocsUrl}</Link>
         </div>
      }
      {/* {
        xml && xml.size ? (<span><br /><span className={propClass}>xml:</span>
          {
            xml.entrySeq().map(([key, v]) => <span key={`${key}-${v}`} className={propClass}><br />&nbsp;&nbsp;&nbsp;{key}: {String(v)}</span>).toArray()
          }
        </span>) : null
      } */}
      {
        enumArray && <EnumModelTable value={enumArray} getComponent={getComponent} />
      }
    </span>
    </td>
  </tr>

</>
  }
}
